:root {
    --primary:#fff;
}
.btn{
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    border: none;
}

.btn--primary{
    background-color: var(--primary);
   
    color: #242424;
    border: 1px solid var(--primary);
}
.btn--outline{
    background-color:transparent;
    
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease  ;
}
.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}
.btn--large{
    padding:12px 26px;
    font-size: 20px;
}

.btn--large:hover, .btn--medium:hover{
    background:#fff;
    color:#242424;
    transition: all 0.3s ease;
}

@media screen and (max-width:700px){
    .btn--primary{
        width: 100%;
    }
}


