.cards{
  padding: 1rem 4rem 4rem 4rem;
  background:white;
}

h1{
  text-align: center;
  margin-bottom: 10px;
}
.cards-container{
  display: flex;
  background: rgb(221, 221, 233);
  align-items: center;
  justify-content: center;
  
  width: 100%;
 
}
.cards-wrapper{
  position:relative;
  margin: 50px 0;
  width: 70%;
}
.cards__items{
  margin-bottom: 25px;
  background: rgb(243, 245, 241);

}
.cards__item{
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 4px;
}
.cards__item__link{
  display: flex;
  flex-direction: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  text-decoration: none;
  overflow: hidden;
}
.cards__item__pic-wrap{
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}
.cards__item__pic-wrap::after{
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}
.card__item__img{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}
.card__item__img:hover {
  transform: scale(1.1);
}
.cards__item__info {
  padding: 20px 30px 30px;
}
.cards__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px
}
@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}
@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}
